@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --text: #f7f7ff;
  --banner: #7d83ff;
  --background: #f7f7ff;
  --header-text: #7d83ff;
  --main-text: #272727;
}

html,
body {
  font-family: "Alata", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  background: #f7f7ff;
  background: var(--background);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: background 200ms;
}

h1,
p {
  margin: 0;
  padding: 0;
  transition-duration: 200ms;
}

.Header {
  background: #f7f7ff;
  background: var(--background);
  transition: background 200ms;
}

.header-title {
  background: #7d83ff;
  background: var(--banner);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 98%;
  margin: 0 auto;
  transition: background 200ms;
}

.header-title h1 {
  color: #f7f7ff;
  color: var(--text);
  margin: 0.5rem 0 0.6rem 2rem;
  font-size: 2rem;
}

.icon-menu svg {
  transition-duration: 200ms;
}

.banner {
  background: #7d83ff;
  background: var(--banner);
  display: flex;
  width: 98%;
  height: 44rem;
  margin: 0 auto;
  overflow: hidden;
  align-items: center;
  border-bottom-left-radius: 4rem;
  border-bottom-right-radius: 4rem;
  transition: background 200ms;
}

.banner img {
  width: 16rem;
  height: 16rem;
  border-radius: 50%;
  border: 0.4rem solid #7d83ff;
  border: 0.4rem solid var(--banner);
  transition: border 200ms;
}

.banner-image {
  margin-left: 4rem;
  border-radius: 50%;
  border: 0.6rem solid #f7f7ff;
  border: 0.6rem solid var(--text);
  transition: border 200ms;
}

.banner-title {
  color: #f7f7ff;
  color: var(--text);
  margin-left: 4rem;
}

.banner-title h1 {
  font-size: 6rem;
}

.banner-title p {
  font-size: 2rem;
}

.card-container {
  display: flex;
  align-items: flex-start;
  margin: -4rem 10% 0 10%;
}

.card {
  background: #f7f7ff;
  background: var(--background);
  margin: 1rem;
  padding: 2rem;
  box-shadow: 0.3rem 0.3rem 2rem rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  transition: background 200ms, transform 200ms;
}

.card h1 {
  color: #272727;
  color: var(--main-text);
  font-size: 2rem;
}

.card p {
  color: #272727;
  color: var(--main-text);
  font-size: 1rem;
}

.card.card-small {
  flex-grow: 1;
}

.card.card-normal {
  flex-grow: 2;
}

.card.card-large {
  flex-grow: 4;
}

.card:hover {
  transform: translateY(-0.2rem);
  transition: transform 200ms;
}

